import "./Video.scss";

import thubmnail from "../../../assets/img/thumbnail-preview.svg";
import youtubeLogo from "../../../assets/img/youtube-logo-red.svg";

export default function Video(props) {
  const {
    imageUrl = thubmnail,
    title = "How NOT to build a PC - The Verge of Awful Computers",
    url = "https://www.youtube.com/watch?v=VE4Ruz_JEf0",
  } = props;

  return (
    <div className="Video" onClick={() => window.open(url)}>
      <img width="100%" src={imageUrl} className="thumbnail" />
      <div className="description">
        <img width="50px" src={youtubeLogo} />
        <div className="content">
          <h1>{title}</h1>
          <p>2 months ago </p>
        </div>
      </div>
    </div>
  );
}
